	module.exports = {"data":{"site":{"siteMetadata":{"title":"gatsby-remark-draw plugin demo","author":"Riaan Hanekom"}},"markdownRemark":{"id":"/home/riaan/Code/Personal/gatsby-remark-draw-demo/src/pages/graphviz/index.md absPath of file >>> MarkdownRemark","html":"<p>Examples taken from the <a href=\"http://graphs.grevian.org/example\">DOT pocket reference</a></p>\n<h2>Example 1: Simple Graph</h2>\n<img src=\"/gatsby-remark-draw-demo/7fec674d085dc9cf05188a21e69edbb0996691b2.svg\" title=\"remark-draw image\">\n<h2>Example 2: K6</h2>\n<img src=\"/gatsby-remark-draw-demo/76d295da0296f0ac823cc939b31c483d565a9aa0.svg\" title=\"remark-draw image\">\n<h2>Example 3: Simple Digraph</h2>\n<img src=\"/gatsby-remark-draw-demo/863ded4f643a5fdd6a950550b41a6492a57e2a71.svg\" title=\"remark-draw image\">\n<h2>Example 4: Full Digraph</h2>\n<img src=\"/gatsby-remark-draw-demo/4959cde4ab0ab8b6be7dcfd0a0d1d85502c7614a.svg\" title=\"remark-draw image\">\n<h2>Example 5: Showing A Path</h2>\n<img src=\"/gatsby-remark-draw-demo/b1c686ffcf5267e8fec60e00204e3e69fff14987.svg\" title=\"remark-draw image\">\n<p><strong>Note that there’s also a shorthand method as follows:</strong></p>\n<img src=\"/gatsby-remark-draw-demo/39de64b70601fa821264f74ead642575c3c8725b.svg\" title=\"remark-draw image\">\n<h2>Example 6: Subgraphs</h2>\n<p>Please note there are some quirks here, First the name of the subgraphs are important, to be visually separated they must be prefixed with cluster_ as shown below, and second only the DOT and FDP layout methods seem to support subgraphs (See the graph generation page for more information on the layout methods)</p>\n<img src=\"/gatsby-remark-draw-demo/7855b3b28d278159646c416440cd4c29a7652cf7.svg\" title=\"remark-draw image\">\n<p>Another Example of a Subgraph, In this example I group nodes together seperately from their edges, And also uses the graph attribute splines=line; to specify that edges should be drawn only as straight lines, no curves allowed.</p>\n<img src=\"/gatsby-remark-draw-demo/76360f8474b6c4d0f22052a449aeaf9c333a6c5d.svg\" title=\"remark-draw image\">\n<h2>Example 7: Large Graphs</h2>\n<p>To make it easier to input large graph descriptions, One may group edges together with a set of braces, It may also help to lay the graph out left to right instead of top to bottom.</p>\n<img src=\"/gatsby-remark-draw-demo/269a93f80d51497d23db5109ad6c29549a119f55.svg\" title=\"remark-draw image\">\n<p>Another feature that can make large graphs manageable is to group nodes together at the same rank, the graph above for example is copied from a specific assignment, but doesn’t look the same because of how the nodes are shifted around to fit in a more space optimal, but less visually simple way. We can make it look much more similar by grouping the nodes together for display as is done in the assignment with rank, as follows</p>\n<img src=\"/gatsby-remark-draw-demo/e8cbc70390c13f01a24f8c36448526e3477775b3.svg\" title=\"remark-draw image\">","frontmatter":{"title":"GraphViz Demo","date":"March 22, 2018"}}},"pathContext":{"slug":"/graphviz/","previous":{"fields":{"slug":"/svgbobrus/"},"frontmatter":{"title":"SvgBobRus demo"}},"next":{"fields":{"slug":"/mermaid/"},"frontmatter":{"title":"Mermaid\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t Demo"}}}}

